.post {
    display: flex;
    flex-direction: column;
    padding: 10px;
    border: 1px solid #6392e3;
    background-color: white;
    border-radius: 5px;
    margin-top: 10px;
    cursor: pointer;
}

.post:hover {
    border: 2px solid #6392e3;
}


.post_info {
    display: flex;
    align-items: center;
}

.post_info > h5 {
    color: #6392e3;
    margin-left: 10px;
    font-size: 13px;
}

.post_info > h5:hover {
    text-decoration: underline;
}


.post_info > small {
    margin-left: 10px;
}



.post_body {
    display: flex;
    flex-direction: column;
}

.post_body > .post_question {
    margin-top: 10px;
    font-weight: bold;
    margin-bottom: 10px;
    display: flex;
}



.post_body > .post_question > p:hover {
    text-decoration: underline;
}

.post_body > img {
    object-fit: contain;
    width: 100%;
    border-radius: 5px;
    cursor: pointer;
}




.post_question > .post_btnAnswer {
    margin-left: auto;
    outline: none;
    border: none;
    background-color: #6392e3;
    color:#fff;
    border-radius: 10px;
    padding: 10px;
}



.post_question > .post_btnAnswer:hover {
    cursor: pointer;
    transform: scale(1.2);

}


.post_footerAction {
    display: flex;
    justify-content: space-around;
    border-radius: 33px;
    padding: 5px;
    background-color:#6392e3;
    align-items: center;
}



.post_footerAction > .MuiSvgIcon-root {
    margin-right:40px;
    cursor: pointer;
    color:black;

}
/* 맨 왼쪽 아이콘 2개 */



.post_footer{
    display: flex;
    align-items: center;
    margin-top:10px;
}

.post_footer > .MuiSvgIcon-root {
    color:black;
    margin-right: 40px;
    cursor: pointer;
}


.post_footerAction > .MuiSvgIcon-root:hover {
    cursor: pointer;
    color:yellow;
    background-color: #6392e3;
}
/* 가운데 아이콘 2개 */


.post_footerRight {
    margin-left: auto;
}
.post_footerRight > .MuiSvgIcon-root {
    cursor: pointer;
    margin-left: 30px;
    color:black;


}

.post_footerRight > .MuiSvgIcon-root:hover {
    cursor: pointer;
    color:yellow;
    background-color: darkgray;

}

/*오른쪽 아이콘 2개 */




.modal_question {

    display: flex;
    align-items: center;
    flex-direction: column;
}

.modal_question > h1 {
    color: #8f1f1b;
    font-weight: 600;
    margin-bottom: 2px;
}

.modal_question > p {
    color: gray;
    font-size: small;
}

.modal_question > p > .name {
    color: black;
    font-weight: bold;
}

.modal_answer {
    display: flex;
    padding-top: 10px;
    flex: 1;
}

.modal_answer > textarea {
    width: 100%;
    height: 250px;
    padding: 5px;
    font-size: 15px;
    color: black;

}

.modal_buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: -20px;
    margin-top: 11px;
    border-top : none !important;
    cursor: pointer;
}

.postGoogle { margin: 0 15px 0 0;}

.modal_buttons > .add {
    border: none;
    outline: none;
    margin-top: 5px;
    flex:0.6;
    background-color: #fff;
    color: #000;
    font-weight: 700;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
}
.modal_buttons > .add:hover {
    background-color: rgb(0, 64, 255);
    color:#fff
 }

.modal_buttons > .can {
    margin-left: auto;
    border: none;
    margin-top: 5px;
    outline: none;
    color: gray;
    font-weight: 500;
    padding: 10px;
    margin-left: 20px;
    border-radius: 33px;
    cursor: pointer;
    flex:0.6;
}
.modal_buttons > .can:hover {
    background-color: rgb(0, 64, 255);
    color:#fff
}







